
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'

import axios from 'axios'

export default {
components: {
Group,
Cell,
CellFormPreview,
axios
},
data () {
return {
list: [],
prices: []
}
},
directives: {
focus: {
inserted: function (el, {value}) {
if (value) {
el.focus()
}
},
update: function (el, {value}) {
if (value) {
el.focus()
}
}
}
},
methods: {
append: function (name, price) {
this.list.push({
label: name,
value: price
})
this.focusStatus = true
},
submit: function (totalPrice) {
let params = {
actual_price: parseFloat(totalPrice),
prices: this.list.map(function (x) {
return {
name: x.label,
price: parseFloat(x.value)
}
})
}
console.log(params)
let result = axios.request({
url: '/api/pindan',
method: 'POST',
baseURL: 'http://localhost:3000',
headers: {'Content-Type': 'application/json'},
data: params
})
.then(response => {
console.log(response)
this.prices = response.data.map(function (x) {
return {
label: x.name,
value: x.price
}
})
})
.catch(function (error) {
alert(error)
})
this.prices = result.map(function (x) {
return {
label: x.name,
value: x.price
}
})
}
}
}



